import React, { useEffect, useMemo, useState } from "react";
import Download from "./Download";
import ImportScript from "./ImportScript";
import Text from "./Text";
import "./styles.css";

import classNames from "classnames";
const Screenplay = ({ selectedLanguage, setText }) => {
  const [importedScreenPlay, setImportedScreenPlay] = useState("");
  const [screenplay, setScreenplay] = useState({
    title: "My Great Screenplay",
    scenes: []
  });
  const [currentScene, setCurrentScene] = useState({
    heading: "",
    action: "",
    characters: [],
    dialogue: [],
    parenthetical: []
  });
  const [currentSceneText, setCurrentSceneText] = useState("");
  const [currentAttribute, setCurrentAttribute] = useState("");
  const [currentText, setCurrentText] = useState("");
  const [tempText, setTempText] = useState([]);
  const [start, setStart] = useState(false);
  const startWriting = () => {
    if (start) {
      handleSubmit();
    }
    setStart(!start);
  };
  const attrs = ["heading", "action", "character", "dialogue", "parenthetical"];
  // useEffect(() => {
  //   console.log("text in attrs", attrs.includes(text));
  //   console.log("text", text);

  //   if (text && attrs.includes(text)) {
  //     setCurrentAttribute(text);
  //   } else if (text.toLowerCase() === "next" || text.includes("next")) {
  //     handleSubmit();
  //   } else if (text.toLowerCase() === "scene") {
  //     handleSave();
  //   } else {
  //     setCurrentText(text);
  //   }
  // }, [text]);
  // useEffect(() => {
  //   handleSubmit();
  // }, [currentAttribute]);
  const handleAddScene = () => {
    if (Object.keys(currentScene).length) {
      const newScreenplay = { ...screenplay };
      newScreenplay.scenes.push(currentScene);
      setScreenplay(newScreenplay);
    }
    setCurrentScene({
      heading: "",
      action: "",
      characters: [],
      dialogue: [],
      parenthetical: []
    });
    setCurrentAttribute("");
    setCurrentText("");
  };

  const handleAddAttribute = (attribute) => {
    setCurrentAttribute(attribute);
  };

  const handleAddText = (e) => {
    setCurrentText(e.target.value);
  };

  const handleSubmit = () => {
    if (currentAttribute === "heading") {
      let heading = "INT.";
      if (currentText.includes("ext")) {
        heading = "EXT.";
      }
      setCurrentScene({ ...currentScene, heading: heading });
    } else if (currentAttribute === "action") {
      setCurrentScene({
        ...currentScene,
        action: currentScene.action + " " + currentText
      });
    } else if (currentAttribute === "character") {
      setCurrentScene({
        ...currentScene,
        characters: [
          ...currentScene.characters,
          currentText.toUpperCase() + ":"
        ]
      });
    } else if (currentAttribute === "dialogue") {
      setCurrentScene({
        ...currentScene,
        dialogue: [...currentScene.dialogue, currentText]
      });
    } else if (currentAttribute === "paranthetical") {
      setCurrentScene({
        ...currentScene,
        parenthetical: [...currentScene.parenthetical, `(${currentText})`]
      });
    }
    setCurrentAttribute("");
    setCurrentText("");
    console.log("current scene", currentScene);
  };

  const handleSave = () => {
    handleAddScene();
    setText(formatScreenplay(screenplay));
    console.log(JSON.stringify(screenplay, null, 2));
    // you can use fetch or axios to save in server
  };

  const formatScreenplay = (screenplay) => {
    let formattedText = "";
    screenplay.scenes.forEach((scene) => {
      formattedText += scene.heading + "\n";
      formattedText += " ".repeat(10) + scene.action + "\n";
      scene.characters.forEach((character) => {
        formattedText += " ".repeat(10) + character + "\n";
      });
      scene.dialogue.forEach((dialogue) => {
        formattedText += " ".repeat(20) + dialogue + "\n";
      });
      scene.parenthetical.forEach((parenthetical) => {
        formattedText += " ".repeat(20) + parenthetical + "\n";
      });
    });

    return formattedText;
  };
  const formattedScreenplay = useMemo(() => {
    if (importedScreenPlay) {
      return importedScreenPlay;
    }
    return formatScreenplay(screenplay);
  }, [importedScreenPlay, screenplay]);

  useEffect(() => {
    const currentSceneText = formatScreenplay({ scenes: [currentScene] });
    setCurrentSceneText(currentSceneText);
  }, [currentScene]);
  const onManualChangeCurrentScene = (ev) => {
    setCurrentSceneText(ev.target.value);
  };
  return (
    <div>
      <Text
        setText={(text) => setCurrentText((ct) => ct + text)}
        start={start}
        text={currentText}
        lang={selectedLanguage}
      />
      <div className="file-options">
        <Download text={formatScreenplay(screenplay)} />
        <ImportScript setText={setImportedScreenPlay} />
        <button onClick={handleSave}>Save</button>
      </div>
      <div className={classNames("action-items")}>
        {currentAttribute && (
          <div className="text-container">
            <input
              type="text"
              onChange={handleAddText}
              value={currentText}
              className="message-input"
              placeholder={currentAttribute}
            />
            {/* <button onClick={handleSubmit}>Submit</button> */}
            <button
              onClick={startWriting}
              className={classNames("start-button", {
                stop: start,
                start: !start
              })}
            >
              {start ? "Stop" : "Start"}
            </button>
          </div>
        )}
        <div
          className={classNames("action-items-mobile ", {
            minimized: start
          })}
        >
          <button
            onClick={() => handleAddAttribute("heading")}
            className="mobile-btn"
          >
            Add Scene Heading
          </button>
          <button
            onClick={() => handleAddAttribute("action")}
            className="mobile-btn"
          >
            Add Action
          </button>
          <button
            onClick={() => handleAddAttribute("character")}
            className="mobile-btn"
          >
            Add Character
          </button>
          <button
            onClick={() => handleAddAttribute("dialogue")}
            className="mobile-btn"
          >
            Add Dialogue
          </button>
          <button
            onClick={() => handleAddAttribute("paranthetical")}
            className="mobile-btn"
          >
            Add Paranthetical
          </button>
        </div>
      </div>

      <div>
        <textarea
          className="screenplay"
          rows={10}
          value={currentSceneText}
          onChange={onManualChangeCurrentScene}
        />
      </div>
      <div>
        <textarea
          className="screenplay"
          rows={10}
          value={formattedScreenplay}
        />
      </div>
    </div>
  );
};

export default Screenplay;
