import React, { useState, useEffect, useRef } from "react";

const SpeechToText = ({ setText, start, text, lang }) => {
  const [transcription, setTranscription] = useState("");
  let recognition = useRef(null);

  useEffect(() => {
    if (!("webkitSpeechRecognition" in window)) {
      console.log(
        "Your browser doesn't support speech recognition or the SpeechGrammarList."
      );
      return;
    }
    let rec = recognition.current;
    rec = new window.webkitSpeechRecognition();
    rec.continuous = true;
    rec.lang = `${lang}-IN`;
    rec.interimResults = true;

    // Create a new SpeechGrammarList and add keywords to it
    const SpeechGrammarList =
      window.SpeechGrammarList || webkitSpeechGrammarList;
    const grammarList = new SpeechGrammarList();
    grammarList.addFromString(
      "#JSGF V1.0; grammar keywords; public <keyword> = action | character | scene;",
      1
    );
    rec.grammars = grammarList;

    rec.onresult = (event) => {
      let interimTranscription = "";
      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          setText(transcription + transcript);
        } else {
          interimTranscription += transcript;
        }
      }
      // setText(interimTranscription);
    };
    if (start) {
      rec.start();
    } else {
      rec.stop();
    }

    return () => {
      rec.stop();
    };
  }, [start]);

  return (
    <div>
      <p>{transcription}</p>
    </div>
  );
};

export default SpeechToText;
